<template>
  <div class="absolute w-full h-full fromup100" :class="{ todown100: downAnimation}">
    <div class="absolute w-full text-center" style="height:75%;top:12.5%">
        <h1 class="text-4xl text-center text-white select-none">Kā Spēlēt?</h1>
        <p class="text-white py-6" style="line-height: 1.8;">Mērķis:<br>
          Savākt pēc iespējas vairāk akmentiņus savā mankalā (lielajā kabatā)<br>
          <br>
          Spēles gaita:<br>
          1) Katrā gājienā spēlētājam ir jāpaņem akmentiņus no vienas savas kabatas un jāieliek tos pa vienam kabatās, virzoties pulksteņrādītāja virzienā.<br>
          (Ja jūs saskaraties ar pretinieka mankalu, tā vietā, lai ieliktu tajā akmentiņu, izlaidiet to)<br>
          2) Ja pēdējais akmentiņš, ko ieliekat, ir jūsu paša mankalā, veiciet papildu gājienu<br>
          3) Ja pēdējais ieliktais akmentiņš ir tukšā kabatā jūsu pusē, sagrābiet visus gabalus šajā un pretējā kabatā un ielieciet tos savā mankalā<br>
          4) Ja nav likumīga gājiena, spēle beidzas, un akmentiņi, kas palikušas katra spēlētāja pusē, tiek savāktas katra spēlētāja mankalā<br>
        </p>
    </div>
    <div class="absolute flex" style="width:10%;height:20%;left:90%">
      <button class="btn-red m-auto w-2/3 h-1/2 text-2xl" @click="backToMenu">EXIT</button>
    </div>
  </div>
  
</template>

<script>

export default {

  data (){
    return {
      downAnimation: false,
    }
  },

  methods: {
    backToMenu() {
      this.disappearAnimation()
      this.$emit('back')
    },

    disappearAnimation(){
      this.downAnimation = true
    },
  }

}
</script>

